.more {
	position: relative;
	&:before {
	    background: #000;
	    left: 0;
	    content: '';
	    top: 0;
	    bottom: 0;
	    height: 100%;
	    width: 410px;
	    right: 0;
	    position: absolute;
    	z-index: -1;
	}
	&__block {
		max-width: 1140px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		&--l {
			padding-top: 145px;
			max-width: 345px;
			width: 100%;
			background: #000;
			height: 100%;
			padding-bottom: 220px;
			font-weight: normal;
			font-family: 'BravoRG';
			font-size: 60px;
			color: #fffefe;
			text-transform: uppercase;
			span {
				color: #a7a7a8;
			}
		}
		&--r {
			width: 42%;
			margin-top: 7%;
			margin-left: 100px;
			height: 100%;
			font-weight: normal;
			font-family: 'RalewayRegular';
			font-size: 14px;
			line-height: 18px;
			color: #000000;
			span {
				font-weight: bold;
			}
			position: relative;
		}
		&--item {
			display: flex;
			align-items: flex-start;
			margin-bottom: 45px;
			p {
				position: relative;
				&:nth-child(1) {
					&:after {
						content: '';
						width: 2px;
						height: 35px;
						content: '';
						background: #000;
						bottom: 60px;
						right: 0;
						position: absolute;
					}
					&:before {
						content: '';
						width: 52px;
						height: 2px;
						content: '';
						background: #000;
						bottom: 0px;
						right: 40px;
						position: absolute;
					}
				}
			}
		}
		&--iteml {
			width: calc(50% - 50px);
			text-align: center;
			margin-right: 50px;
		}
		&--itemr {
			width: 50%;
			padding-left: 30px;
			font-weight: normal;
			font-family: 'RalewayRegular';
			font-size: 14px;
			line-height: 18px;
		}
		&--title {
			
			font-weight: normal;
			font-family: 'RalewayRegular';
			font-size: 24px;
			line-height: 27px;
			margin-bottom: 40px;
			text-align: left;
		}
	}
}