.red {
	position: relative;
	&:before {
	    background: #000;
	    left: 0;
	    content: '';
	    top: 0;
	    bottom: 0;
	    height: 100%;
	    width: 410px;
	    right: 0;
	    position: absolute;
    	z-index: -1;
	}
	&__block {

		max-width: 1140px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		align-items: flex-start;
		background: url("../../img/bg_s.png") 50% 10% no-repeat;	
		&--l {

			padding-top: 145px;
			max-width: 345px;
			width: 150%;
			background: #000;
			height: 100%;
			padding-bottom: 221px;
			font-weight: normal;
			font-family: 'BravoRG';
			font-size: 60px;
			color: #fffefe;
			text-transform: uppercase;
			flex: 0 0 345px;
			span {
				color: #a7a7a8;
			}
		}
		&--r {
			padding-top: 7%;
			letter-spacing: 1px;
			width: 50%;
			margin-left: 6%;
		    display: flex;
    		align-items: flex-start;
		}
		&--textl {
			width: 30%;
			font-weight: normal;
			font-family: 'RalewayRegular';
			font-size: 14px;
			color: #000000;
			line-height: 18px;
			position: relative;
			&:before {
				content: '';
				width: 2px;
				height: 116px;
				top: 10px;
				background: #000;
				right: -10px;
				position: absolute;
			}
			span {
				padding-bottom: 150px;
				padding-right: 20px;
				display: block;
				background: url(../../img/cube_bg.png) 0% 115% no-repeat,
							url(../../img/main_arrow.png) 0% -10% no-repeat;
				-webkit-background-size: 80%;
				background-size: 80%;
				margin-top: 17%;
				font-weight: normal;
				font-family: 'RalewayRegular';
				font-size: 24px;
				line-height: 27px;
				position: relative;
				&:before {
					position: absolute;
					content: '';
					top: -60px;
					right: 0;
					height: 1px;
					width: 56px;
					background: #000;
				}
			}
		}
		&--textr {
			width: 25%;
			margin-left: 50px;
			text-align: center;
			ul {
				text-align: left;
				margin: 0;
				padding: 0;
				margin-left: 10px;
				li {
					color: #d9d8d8;
					margin-bottom: 20px;
					span {

						color: #000;
						font-weight: normal;
						font-family: 'RalewayRegular';
						font-size: 14px;
					}
				}
			}
			p {
				text-align: left;
				font-weight: normal;
				font-family: 'RalewayRegular';
				font-size: 14px;
				line-height: 18px;
			}
			position: relative;
			&:before {
				position: absolute;
				content: '';
				width: 106px;
				height: 2px;
				bottom: 10px;
				right: 10px;
				background: #000;
			}
			&:after {
				position: absolute;
				content: '';
				height: 37px;
				width: 2px;
				bottom: 80px;
				right: -5px;
				background: #000;
			}
			
		}
	}
}
